<template>
  <div
    name="mycal"
    class="mycal my-content"
  >
    <div class="top">
      <form
        class="form-inline"
        style="height:385px;"
      >
        <div class="form-group col-xs-6 left edit-block">
          <label>Home Status</label>
          <input
            type="number"
            class="form-control"
            v-model="model.homeRank"
          />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>Guest Status</label>
          <input
            type="number"
            class="form-control"
            v-model="model.guestRank"
          />
        </div>
        <div class="form-group col-xs-12 left edit-block">
          <label>Compare HS</label>
          <input
            type="number"
            class="form-control"
            v-model="model.hgx"
          />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>HS</label>
          <input
            type="number"
            class="form-control"
            v-model="model.hs"
          />
        </div>
        <div class="form-group col-xs-6 left edit-block">
          <label>GS</label>
          <input
            type="number"
            class="form-control"
            v-model="model.gs"
            @keyup.enter="calculate"
          />
        </div>
      </form>
      <div style="padding:15px 15px 0 15px;">
        <table class="table table-striped table-bordered">
          <thead>
            <tr>
              <th width="33%">HS</th>
              <th width="33%">GS</th>
              <th>H/G</th>
            </tr>
          </thead>
          <tbody>
            <tr class="success">
              <td>{{hs}}</td>
              <td>{{gs}}</td>
              <td>{{hs-gs}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="button-area">
      <div class="btn-group">
        <button
          class="btn btn-danger"
          v-on:click='calculate'
        >Calculate</button>
        <button
          class="btn btn-success"
          v-on:click='resetStore'
        >{{message}}</button>
      </div>
    </div>
  </div>
</template>
<script>
// import Store from '../services/store';
import DB from '../services/db';
export default {
  name: 'mycal',
  data() {
    return {
      model: {
        homeRank: '',
        guestRank: '',
        hgx: '',
        hs: '',
        gs: ''
      },
      hsFinal: '',
      gsFinal: '',
      message: 'Winner!',
      // modelList: Store.fetch().length > 0 ? Store.fetch() : [],
      name: ''
    };
  },
  methods: {
    calculate: function () {
      // 转为数字
      for (let item in this.model) {
        if (item !== 'name') {
          this.model[item] = parseFloat(this.model[item]);
        }
      }
      let l = 1;
      let r = 1;
      if (this.model.homeRank > 3) {
        l = l * (1 + Math.round((this.model.homeRank - 3) / 2) * 0.1);
      } else if (this.model.homeRank < -3) {
        l = l * (1 + Math.round((this.model.homeRank + 3) / 2) * 0.1);
      }
      if (this.model.guestRank > 3) {
        r = r * (1 + Math.round((this.model.guestRank - 3) / 2) * 0.1);
      } else if (this.model.guestRank < -3) {
        r = r * (1 + Math.round((this.model.guestRank + 3) / 2) * 0.1);
      }
      if (this.model.hgx > 0) {
        l *= (1 + Math.round((this.model.hgx) / 2) * 0.1);
      } else {
        r *= (1 - Math.round((this.model.hgx) / 2) * 0.1);
      }
      this.hs = (this.model.hs * l).toFixed(2);
      this.gs = (this.model.gs * r).toFixed(2);
      this.model = {
        homeRank: '',
        guestRank: '',
        hgx: ''
      };
    },
    // addToList: async function (obj) {
    //   const rs = await DB.add(obj);
    // },
    resetStore: function () {
      if (window.confirm('确定清空储存？')) {
        DB.clear();
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.left:nth-child(2n) {
  border-right: 1px solid cornflowerblue;
}

.btn-default {
  width: 110px;
  color: #333;
  background-color: #fff;
  border-color: #ccc;
}

.record_list_model:nth-child(2n + 1) input {
  border: 5px groove #289ed4;
}

.add_record_btn {
  border-top: 2px solid;
  padding-top: 5px;
}

.record_btn_style {
  width: 49%;
  float: left;
  text-decoration: none;
  background: indianred;
  margin-top: 2px;
  padding: 0;
  height: 40px;
  line-height: 40px;
  font-size: 1.8rem;
}
.edit-block {
  display: flex !important;
  height: 55px;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}
.form-group {
  margin-bottom: 0;
}
</style>



